import { Grid } from "@material-ui/core";
import React from "react";
import Layout from "../../components/layout/Layout";
import { PacMan } from "./PacMan";

export default function PacFrog() {
    return(
		<Layout>
			<Grid container spacing={2}>
				<Grid item xs={5}>
					<div className="box" style={{padding: 20}}>
						<div className="titleText secondary">PacFrog</div>
					</div>
					<div style={{padding: 20, color: 'white'}}>
						In a previous iteration of my website, I wrote a script where a Pac-Man figure would chase around your cursor inside a
						div.  It is quite rudimentary.  Not sure what I'll do with it yet...
					</div>
				</Grid>
				<Grid item xs={12}>
					<PacMan />
				</Grid>
			</Grid>
		</Layout>
    )
}